<template>
    <div>
        <!-- The nav bar -->
        <nav class="navbar navbar-expand-lg bg-secondary navbar-dark py-3 fixed-top">
            <div class="container">
                <a href="#" class="navbar-brand">Log In</a>
            </div>
        </nav>

        <!-- the log in page -->
        <section>
            <div class="container pt-4">

                <div class="input-group mb-3">
                    <span class="input-group-text" ><i class="bi bi-person-circle"></i></span>
                    <input type="text" class="form-control"
                           v-model="unam" :class="{ 'is-invalid': unamWrong }"
                    >
                </div>
                <div class="input-group mb-3">
                    <span class="input-group-text" ><i class="bi bi-lock-fill"></i></span>
                    <input type="password" class="form-control"
                           v-model="pswd" :class="{ 'is-invalid': pswdWrong}"
                    >
                </div>

                <!-- the messages -->

                <div v-show="unamWrong" class="alert alert-danger" role="alert">
                    Username empty
                </div>
                <div v-show="pswdWrong" class="alert alert-danger" role="alert">
                    Password empty
                </div>

                <div v-show="msg !== ''" class="alert alert-danger" role="alert">
                    {{msg}}
                </div>

                <!-- the submit buttons -->
                <div class="d-grid gap-2 mt-3 mx-auto d-md-flex justify-content-md-end"
                     style="grid-template-columns: repeat(3, 1fr);"
                >
                    <button class="btn btn-info my-login-btn" type="button" @click="toSignUp">
                        <router-link to="/signup">
                            Sign Up
                        </router-link>
                    </button>
                    <button class="btn btn-success my-login-btn" type="button" @click="send">
                        <span class="h3">
                            <i class="bi bi-check"></i>
                        </span>
                    </button>
                    <button class="btn btn-secondary my-login-btn" type="button" @click="toHome">
                        <span class="h3">
                            <router-link to="/">
                                <i class="bi bi-x"></i>
                            </router-link>
                        </span>
                    </button>
                </div>

            </div>
        </section>

        <!-- the spinner -->
        <section v-show="loading">
            <div class="container">
                <div class="d-flex justify-content-center">
                    <div class="spinner-border m-5" role="status" style="width: 3rem; height: 3rem;">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div>
            </div>
        </section>

    </div>
</template>

<script>
 import axios from "axios"

 export default {
     data() {
         return {
             unam: 'aaa',
             pswd: '123',
             loading: false,
             msg: '',
             http: axios.create({
                 baseURL: process.env.VUE_APP_AUTH_HOST? `http://${process.env.VUE_APP_AUTH_HOST}:8002` : 'http://localhost:8002'
             }),
         }
     },
     created: function(){
         console.log(`Login created() called with BACKEND = ${this.http.getUri()} `)
     },
     methods: {
         toSignUp() {
             this.$router.push({path: '/signup'})
         },
         toHome() {
             this.$router.push({path: '/'})
         },
         async send() {
             if (this.unamWrong) {
                 return;/* empty username */
             }
             if (this.pswdWrong) {
                 return;/* empty password */
             }

             console.log(`Try logging in  as (${this.unam}, ${this.pswd})`)
             this.loading = true

             let data
             if (this.online) {
                 await this.http.post('login/',
                                      {unam: this.unam, pswd: this.pswd}).then( res => {
                                          data = res.data
                                      }).catch(handelError)
                 this.loading = false
                 if (data.ok) {
                     console.log(`login okay with ${this.unam}`)
                     this.$store.commit('setUnam', this.unam)
                     this.$router.push({path: '/'})

                 } else {
                     console.log(`login failed with ${this.unam}`)
                     this.msg = data.msg
                 }
             } else {
                 //a wait for 3 sec
                 setTimeout(()=> {
                     this.loading = false
                     console.log(`done`)
                     this.$store.commit('setUnam',this.unam)
                     this.$router.push({path: '/'})
                 },3000)
             }
         },
     },
     computed: {
         unamWrong() {return this.unam === ''},
         pswdWrong() {return this.pswd === ''},
         online() {
             return this.$store?.state.online
         },
     }
 }

 function handelError(err){
     if (err.response) {
         // The request was made and the server responded with a status code
         // that falls out of the range of 2xx
         console.log(err.response.data);
         console.log(err.response.status);
         console.log(err.response.headers);
     } else if (err.request) {
         // The request was made but no response was received
         // `err.request` is an instance of XMLHttpRequest in the browser and an instance of
         // http.ClientRequest in node.js
         console.log(err.request);
     } else {
         // Something happened in setting up the request that triggered an Err
         console.log('Err', err.message);
     }
     console.log(err.config);
 }
</script>
