import { createStore } from 'vuex'

export default createStore({
  state: {
    unam: '',
    online: false,
  },
  getters: {
  },
  mutations: {
    setUnam(state, unam){
      state.unam = unam
    },
    toggleOnline(state){
      state.online = !state.online
    }
  },
  actions: {
  },
  modules: {
  }
})
