<template>
    <div>
        <!-- The nav bar -->
        <nav class="navbar navbar-expand-lg bg-secondary navbar-dark py-3 fixed-top">
            <div class="container">
                <a href="#" class="navbar-brand">A Todo App</a>
                <button
                    class="navbar-toggler"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navmenu"
                >
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navmenu">
                    <ul class="navbar-nav ms-auto text-end">
                        
                        <!-- <li class="nav-item mt-2">
                            <a href="#" class="">
                                <button type="button" class="btn btn-primary" @click="toggleOnline">Toggle Online</button>
                            </a>
                        </li> -->

                        <li v-if="online" class="nav-item mt-2">
                                <span class="badge text-bg-success" @click="toggleOnline"><i class="bi bi-wifi"></i></span>
                        </li>
                        <li v-else class="nav-item mt-2">
                                <span class="badge text-bg-success" @click="toggleOnline"><i class="bi bi-wifi-off"></i></span>
                        </li>

                        <li v-show="unam !== ''" class="nav-item mt-2">
                            <a href="#" class="nav-link">
                                Logged in as:
                                <span class="badge text-bg-success">{{unam}}</span>
                            </a>
                        </li>
                        <li v-show="unam !== ''" class="nav-item mt-2">
                            <a href="#" class="">
                                <button type="button" class="btn btn-danger" @click="logOut">Log out</button>
                            </a>
                        </li>

                        <li v-show="unam === ''" class="nav-item mt-2 mx-lg-2">
                            <!-- make some margin on large screen -->
                            <a href="#" class="">
                                <button type="button" class="btn btn-success">
                                    <!-- <router-link to="/">Home</router-link> | -->
                                    <router-link to="/login">Log In / Sign Up</router-link>
                                </button>
                            </a>
                        </li>

                        <li class="nav-item mt-2 mx-lg-2">
                            <a href="#" class="">
                                <button type="button" class="btn btn-info">
                                    <router-link to="/about">About</router-link>
                                </button>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

        <!-- the input bar -->
        <section class="fixed-bottom">
            <div class="container bg-light">
                <div class="input-group mb-3 ">
                    <input @keyup.enter="addTodo" type="text" class="form-control" v-model="input" placeholder="Write your todo here">
                    <button @click="addTodo" class="btn btn-success" type="button" v-show="input !== ''" >Add</button>
                </div>
            </div>
        </section>
        <!-- show items -->
        <section>
            <div class="container pt-4">
                <!-- Use grid -->

                <div class="row mx-3 rounded bg-light border lead mb-2"
                     v-for="(todo,index) in todos" :key="index">
                    <div class="col-2 col-md-1 rounded-start text-center" @click="toggleDone(index)">
                        <i v-if="!todo.done" class="bi bi-square"></i>
                        <i v-else class="bi bi-check-square"></i>
                    </div>
                    <div class="col-8 col-md-10">
                        <del v-if="todo.done" class="text-muted">{{todo.name}}</del>
                        <span v-else>{{todo.name}}</span>
                    </div>
                    <div class="col-2 col-md-1 rounded-end text-center" @click="remove(index)">
                        <i class="bi bi-x-square-fill"></i>
                    </div>
                </div>

            </div>
        </section>
    </div>
</template>

<script>
 import axios from "axios"

 export default {
     name: 'HelloWorld',
     data() {
         return {
             input: "",
             todos: [
                 /* { name: 'aaaaaaa', done: false,},
                  * { name: 'bbbbbbb', done: false,}, */
             ],
             http: axios.create({
                 baseURL: process.env.VUE_APP_BACKEND_HOST ? `http://${process.env.VUE_APP_BACKEND_HOST}:8001/todos` :'http://localhost:8001/todos'
             }),
         }
     },
     created: function(){
         console.log(`HelloWorld created() called with BACKEND = ${this.http.getUri()} `)
         if (this.online && this.unam && this.unam !== '') this.fetchAll()
     },
     methods: {
         remove(i){
             console.log(`Removing ${i}`)
             this.todos.splice(i,1)/* remove the  ith element*/
             this.sync()
         },
         toggleDone(index){
             console.log(`Toggling for ${index}`)
             this.todos[index].done =!this.todos[index].done
             this.sync()
         },
         addTodo(){
             console.log(`Adding todo ${this.input}`)
             this.todos.push({name: this.input, done: false})
             this.input=''
             this.sync()
         },
         async fetchAll(){
             // fetch All from remote
             let remote_todos
             let nam = this.todos.map(t => t.name) // local todos
             /* console.log(`local todos: ${nam}`) */

             console.log(`fetching data for ${this.unam}`)

             await this.http.post('/getall/', {unam: this.unam}).then(response =>{
                 remote_todos = response.data
             }).catch(handelError)


             for (let r of remote_todos){
                 console.log(`Got remote item {name:${r.name}, done:${r.done}}`)

                 if (!nam.includes(r.name)){
                     console.log(`\tAdding this`)
                     this.todos.push({name: r.name, done: r.done})
                 }
             }
         },
         async sync(){if (this.online) await this._sync()},
         async _sync(){
             // Implementation 1:
             // delete all in remote
             await this.http.delete('/',{data: {unam: this.unam}}).catch(err => console.write(err))
             // Upload the local ones
             for (let t of this.todos){
                 await this.http.post('/',{name: t.name, done: t.done, unam: this.unam}).catch(handelError)
             }

             // Implementation 2:
             // Fetch all todos
             // for each remote item: delete it if it's not local, update it if it's local
             // for each local item: add to remote if dosen't already exist
         },
         logOut() {
             this.$store.commit('setUnam','')
             this.todos = []
         },
         toggleOnline() {
             this.$store.commit('toggleOnline')
         }
     },
     computed: {
         unam() {
             return this.$store?.state.unam
         },
         online() {
             return this.$store?.state.online
            //  return false
         }
     }
 }


 function handelError(err){
     if (err.response) {
         // The request was made and the server responded with a status code
         // that falls out of the range of 2xx
         console.log(err.response.data);
         console.log(err.response.status);
         console.log(err.response.headers);
     } else if (err.request) {
         // The request was made but no response was received
         // `err.request` is an instance of XMLHttpRequest in the browser and an instance of
         // http.ClientRequest in node.js
         console.log(err.request);
     } else {
         // Something happened in setting up the request that triggered an Err
         console.log('Err', err.message);
     }
     console.log(err.config);
 }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 a button {
     min-width : 100px;
 }
</style>
